<template>
	<div class="app-content" v-if="!!Object.keys(content).length">
		<div class="container">
			<div class="page-title">
				<h1>Общие настройки</h1>
				<div class="actions">
					<button class="btn btn-success" @click="savePage()" :disabled="saveInProgress">
						<span class="fas fa-save"></span>
						Сохранить
					</button>
				</div>
			</div>
			<div class="page-content">
				<div class="progress" v-if="saveInProgress">
					<div class="progress-bar progress-bar-striped progress-bar-animated" role="progressbar" style="width: 100%"></div>
				</div>
				<div class="alert alert-success" v-if="isSavedSuccess" role="alert">
					Изменения успешно сохранены
				</div>
				<fieldset>
                    <legend>Список меню</legend>
					<div class="row">
                        <div class="col-12">
                            <label class="form-label">Главная</label>
                            <InputLangGroup v-model="content.menu.home" :content="content.menu.home"/>
                        </div>
                        <div class="col-12">
                            <label class="form-label">О нас</label>
                            <InputLangGroup v-model="content.menu.about" :content="content.menu.about"/>
                        </div>
                        <div class="col-12">
                            <label class="form-label">Каталог</label>
                            <InputLangGroup v-model="content.menu.catalog" :content="content.menu.catalog"/>
                        </div>
                        <div class="col-12">
                            <label class="form-label">Вопросы и ответы</label>
                            <InputLangGroup v-model="content.menu.faq" :content="content.menu.faq"/>
                        </div>
                        <div class="col-12">
                            <label class="form-label">Для партнеров</label>
                            <InputLangGroup v-model="content.menu.forPartners" :content="content.menu.forPartners"/>
                        </div>
                        <div class="col-12">
                            <label class="form-label">Контакты</label>
                            <InputLangGroup v-model="content.menu.contacts" :content="content.menu.contacts"/>
                        </div>
					</div>
				</fieldset>
				<fieldset>
					<legend>Глобальные эллементы</legend>
					<div class="row">
						<div class="col-12">
                            <label class="form-label">Текст в поле поиска (шапка)</label>
                            <InputLangGroup v-model="content.headerSearchFieldText" :content="content.headerSearchFieldText"/>
                        </div>
                        <div class="col-12">
                            <label class="form-label">Текст в подвале сайта</label>
                            <InputLangGroup v-model="content.footerInfoText" :content="content.footerInfoText" :isTextarea="true"/>
                        </div>
                        <div class="col-12">
                            <label class="form-label">Копирайт</label>
                            <InputLangGroup v-model="content.copyrightText" :content="content.copyrightText"/>
                        </div>
					</div>
				</fieldset>
                <fieldset>
					<legend>Форма обратной связи</legend>
					<div class="row">
						<div class="col-12">
                            <label class="form-label">Заголовок</label>
                            <InputLangGroup v-model="content.callbackForm.title" :content="content.callbackForm.title"/>
                        </div>
						<div class="col-12">
                            <label class="form-label">Текст</label>
                            <InputLangGroup v-model="content.callbackForm.text" :content="content.callbackForm.text"/>
                        </div>
						<div class="col-12">
                            <label class="form-label">Подсказка</label>
                            <InputLangGroup v-model="content.callbackForm.placeholder" :content="content.callbackForm.placeholder"/>
                        </div>
					</div>
				</fieldset>
			</div>
		</div>
	</div>
</template>

<script>
import InputLangGroup from '../components/InputLangGroup.vue';

export default {
    data() {
        return {
            content: '',
            saveInProgress: false,
            isSavedSuccess: false
        }
    },
    components: {
        InputLangGroup
    },
    computed: {
        pageContent() {
            return this.$store.getters['getGeneralInfo'];
        }
    },
    watch: {
        pageContent(value) {
            this.content = value;
        }
    },
    methods: {
        savePage() {
            this.saveInProgress = true;
            this.$store.dispatch('saveGeneralInfo', this.content)
                .then(() => {
                    this.saveInProgress = false;
                    this.isSavedSuccess = true;
                    setTimeout(() => {
                        this.isSavedSuccess = false;
                    }, 3000);
                });
        }
    },
    created() {
        this.content = this.pageContent;
    },
    mounted() {

    }
};
</script>